const generateButton = document.getElementById('generate-button');
const originLink = document.getElementById('origin-link');
const discountCode = document.getElementById('discount-code');

const output = document.getElementById('output');

generateButton?.addEventListener('click', () => {

    if (!originLink || !discountCode) {
        alert('Please enter link and discount code');
        return;
    }

    if (!output) {
        return;
    }

    if (!(originLink instanceof HTMLInputElement) || !(discountCode instanceof HTMLInputElement)) {
        return;
    }

    const link = originLink.value;
    const code = discountCode.value;

    const result = generateLink(link, code);

    output.innerHTML = result;
});

const generateLink = (link: string, code: string) => {
    const url = new URL(link);
    const discountFragment = `/discount/${code}`;

    let domain = url.hostname;
    let params = url.searchParams;
    console.log(params);

    return `${domain}/${discountFragment}/`;
}

